const Taro = require('./dist/index.js').default

let api

// bundler  require
if (process.env.TARO_ENV === 'alipay') {
  api = require('./lib/alipay')
} else if (process.env.TARO_ENV === 'jd') {
  api = require('./lib/jd')
} else if (process.env.TARO_ENV === 'qq') {
  api = require('./lib/qq')
} else if (process.env.TARO_ENV === 'swan') {
  api = require('./lib/swan')
} else if (process.env.TARO_ENV === 'tt') {
  api = require('./lib/tt')
} else if (process.env.TARO_ENV === 'weapp') {
  api = require('./lib/wx')
} else if (process.env.TARO_ENV === 'h5') {
  api = require('@tarojs/router')
  for (const key in api) {
    if (api.hasOwnProperty(key)) {
      Taro[key] = api[key]
    }
  }
}

//  import  Jest, rollup
const initNativeAPI = api && api.default ? api.default : api
//  env type h5
if (typeof initNativeAPI === 'function') {
  initNativeAPI(Taro)
}

module.exports = Taro
module.exports.default = module.exports
